.form-div {
  padding: 24px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 8px 0px 0px rgba(0, 0, 0, 0.15);
  text-align: center;
}

.form-div p {
  color: #bab7d4;
  font-size: 11px;
  font-weight: 500;
  line-height: 21px; /* 190.909% */
}

.form-div p span {
  color: var(--color-red);
  font-weight: 700;
  cursor: pointer;
}

.trial-form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 16px;
  margin-bottom: 8px;
}

.trial-form input,
.trial-form button {
  width: 100%;
  border: none;
  outline: none;
  font-family: "Poppins", sans-serif;
  padding: 15px 20px;
}

.trial-form input {
  border-radius: 5px;
  border: 1px solid #dedede;
  color: #3d3b48;
  font-size: 14px;
  font-weight: 600;
  line-height: 26px; /* 185.714% */
  letter-spacing: 0.25px;
}

.trial-form button {
  border-radius: 5px;
  background: #38cc8b;
  box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.09) inset;
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  line-height: 26px; /* 173.333% */
  letter-spacing: 1px;
  transition: all 0.3s ease-in-out;
}

.trial-form button:hover {
  cursor: pointer;
  background: #77e2b3;
  box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.09) inset;
}

.form-div .warning {
  margin-top: -15px;
  color: var(--color-red);
  font-size: 11px;
  font-style: italic;
  font-weight: 500;
}

.trial-form .warning-input {
  border: 2px solid var(--color-red);
  background-image: url(/public/images/icon-error.svg);
  background-repeat: no-repeat;
  background-position: 95% center;
}
